#admin-page {
	width: 80%;
	margin: 0 auto;
}
.activity-table {
	padding: 20px 10px 30px 10px;
	margin: 60px 0;
	box-shadow: 0 0 8px 0 #c7c7c7;

}
#admin-page table {
	border-spacing: 0;
	width:100%;
	font-size: 14px;
}
#admin-page th {
	text-align: left;
	padding: 10px 20px;
	margin: 0;
	border-bottom: 2px solid #e9ecef;
}
#admin-page td {
	padding: 10px 20px;
	margin: 0;
	border-bottom: 1px solid #e9ecef;
}
#admin-page tr:last-child td{
	border-bottom: 0;
}
#admin-page .table-header {
	text-align: left;
	font-size: 16px;
	margin: 24px 20px 28px 20px;
	font-weight: bold;
	text-decoration: underline;
}
#admin-page .type {
	text-transform: capitalize;
	font-size: 11px;
	border-radius: 10rem;
	padding: 2px 10px;
	font-weight: bold;
	display: inline-block;
}
/* type colors */
.red {
	color: #d32f2f;
	background-color: #ffebee;
}

.yellow {
	color: #f57c00;
	background-color: #fff3e0;
}
.green {
	color: #388e3c;
	background-color: #e8f5e9;
}

/* style modal */
.modal {
	position: fixed;
    top: 50%;
    left: 50%;
    background-color: white;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 20px 30px;
    
}


